import './App.css';
import ThemeSelector from './ThemeSelector';
import { useState } from 'react';
import { CodeBlock, obsidian } from "react-code-blocks";
import raw from 'raw.macro';
import Zoom from 'react-medium-image-zoom'
import ReactPlayer from 'react-player'
import 'react-medium-image-zoom/dist/styles.css'


function App() {
  const [visible, setVisible] = useState({1:false, 2:false, 3:false, 4:false, 5:false, 6:false, 7:false});
  const A1code = raw('./A1code.txt');
  const A2code = raw('./A2code.txt');
  const A3code = raw('./A3code.txt');
  const A4code = raw('./A4code.txt');
  const A5code = raw('./A5code.txt');
  const A6acode = raw('./A6acode.txt');
  const A6jcode = raw('./A6jcode.txt');
  const A6hcode = raw('./A6hcode.txt');
  const ChessCode = raw('./Chess.txt')

  return (
    <div className="App">

        <div className="theme-selector">
          <ThemeSelector/>
          <h1>Welcome to my 439 class page!</h1>
          <img className="picture" src="./me.jpg"/>
          <h3 className="body-text">
              Hello! My name is Edgar! I'm a senior in HCDE taking the data science pathway. I have a cat, she is very soft and fluffy.
              And I like food. Cinnamon rolls especially are yummy.
          </h3>
          <div className="content">

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 1: !visible[1]}))}}>
              <h3 className="menu-text">Assignment 1: Blink!</h3>
            </div>
            {visible && visible[1] && 
              <div style={{textAlign: "left"}}>
                <h2>REASONING</h2>
                <h3>
                  I used green, red, and "yellow" (they look orange to me) LEDs, each one having a voltage drop of 1.8V. 
                  The Arduino Uno gets 5V from the USB port. So subtracting the voltage drop of a single LED from the 
                  volts coming from the source, 5V - 1.8V, we're left with 3.2 volts. Each led has a current of 20 mA (or 0.02 A). 
                  So to find the resistance needed, I divided the volts by the current (3.2 volts / 0.02 A) and got 160 ohms. 

                  But wait, there's more. Since I'm doing a charlieplex system, each circuit encounters two resistors. So I divided
                  the 160 ohms in half to get 80 ohms. Ideally each pin needs 80 ohms of resistance. I don't have 80 ohm resistors,
                  so I used 100 ohm resistors instead. Close enough.
                </h3>
                <h2>THEORETICAL BLINKY THINGS</h2>
                <Zoom>
                  <img src="A1.svg" style={{width: "65vw"}}/> 
                </Zoom>
                <br/>
                <h2>THE BLINKY THINGS IN ACTION</h2>
                <Zoom>
                  <img src="charlie_web.gif" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a1_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a1_2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                
                <br/>
                <h2>THE CODE</h2>
                {/* <div style={{backgroundColor: "rgba(102, 102, 102, 0.3)", maxWidth:"70%", position:"relative", left:"15%"}}> */}
                <CodeBlock text={A1code} language="arduino" theme={obsidian}/>
                {/* </div> */}
              </div>
            }

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 2: !visible[2]}))}}>
              <h3 className="menu-text">Assignment 2: Fade!</h3>
            </div>
            {visible && visible[2] &&
              <div style={{textAlign: "left"}}>
                <h2>REASONING</h2>
                <h3>
                  For this project, I have two RGB LEDs that light up and fade over time. The color of the LEDs
                  depend on which button is pressed; blue button makes them light up blue, etc. The time it takes for
                  them to fade depends on the potentiometer. If it's twisted all the way to the left, the lights will light up
                  and fade in moments. The more it's twisted to the right, the longer it takes. Adjusting the potentiometer
                  while the LEDs are fading will either speed them up or slow them down, depending on direction. 
                  If the LEDs are currently on, the arduino will ignore all button presses. Once they're done fading,
                  the buttons will once again be able to restart the LEDs. <br/><br/>

                  All non-ground LED pins are connected to 220 OHM resistors. Red and green have a voltage drop of 1.8v (according
                  to the LED slides), 5v (from usb power) - 1.8v / 0.02A (desired current) = 160 ohms. Blue has a voltage drop of
                  3.3v, 5v - 3.3v / 0.02A = 85 ohms. Due to laziness I stuck 220 ohm resistors on all of them. There's no danger
                  going over optimal resistance, it just makes the LEDs a bit dimmer. 220 ohm isn't a big enough difference 
                  to make the LEDs TOO dim for my purposes. <br/><br/>

                  The three tactile switches and the potentiometer have 10k resistors connected. According to professor Nadya on
                  discord, in order for buttons (and the potentiometer I would assume) to work, a nominal amount of current is needed
                  and the lower the current is, the less power used. I did further googling to find out that resistors in these situations
                  are called pull-up resistors, and 10k ohms is the general rule of thumb to project against onboard noise and reduce power 
                  wasted. <br/><br/>

                  Also I hadn't realized digitalWrite() is also a requirement until the very end, so as a bonus the built-in LED
                  now turns on whenever an LED is on. Once the built-in LED turns off you're free to click buttons again.
                </h3>

                <h2>THEORETICAL FADE-Y THINGS</h2>
                <Zoom>
                  <img src="a2.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>FADE-Y LIGHTS IN ACTION</h2>
                <Zoom>
                  <img src="hw2.gif" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a2_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a2_2.jpg" style={{height: "35vh"}}/>
                </Zoom>

                <h2>THE CODE</h2>
                <CodeBlock text={A2code} language="arduino" theme={obsidian}/>
              </div>
            }

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 3: !visible[3]}))}}>
              <h3 className="menu-text">Assignment 3: Input Output!</h3>
            </div>
            {visible && visible[3] && 
              <div style={{textAlign: "left"}}>
                <h2>CUSTOMIZABLE SOLAR LIGHT</h2>
                <h3>
                  Three buttons, one for each RGB color. If you hold a button down, the potentiometer can be used to adjust how much 
                  of that color is used. So if red is held down and twisted all the way left, the RGB will have 255 red, twist all the 
                  way to the right, 0 red. Adjusting each color allows the RGB to output a wide range of colors. The light only turns
                  on when the photoresistor detects it's dark out. As seen in the gif the light only turns on when a shadow covers the
                  circuit.<br/><br/>

                  Same as last assignments, LED's use 220 ohm resistors, buttons and potentiometer use 10k resistors. <br/><br/>

                  New to this assignment though, is the photoresistor. Looking at the diagram, resistor R8 is a 10k resistor being
                  used as a voltage divider. Photoresistor resistance decreases as it is exposed to light. So with very bright light,
                  (like shining a flashlight right at the sensor), it's going to read out 1023, which corresponds to 5v. That means
                  the photoresistor is not providing any resistance and the full 5v are passing through. When the photoresistor has 
                  very little light (like when you make contact and cover the sensor), sensor readings will print out near zero, and
                  theoritically should print out zero although it's a bit difficult to make it not detect any light. I'm not sure exactly
                  how the readings correspond to voltage values, but I'm just going to say that when the reader is near zero, 
                  the voltage being read is also near zero, let's say 0.01 for example purposes. So if 0.01 voltage is being passed through,
                  (0.01/5 volts in)*R1 + 10k ohms = 10kohms <br/>
                  R1 = 10kohms / (0.01 volts out/5 volts in) - 10k <br></br>
                  R1 = 499k ohms <br></br><br></br>

                  So assuming 0.01 volts are getting across, the photoresistor is providing uh... close to 500k ohms of resistance.... <br></br><br></br>

                  yeah I'm not too sure about that but I'm hungry and the deadline is close so uh...yeah! Math!
                </h3>

                <h2>SCHEMATIC</h2>
                <Zoom>
                  <img src="a3.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>LIGHTS</h2>
                <Zoom>
                  <img src="hw3.gif" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw3_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw3_2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw3_3.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw3_4.jpg" style={{height: "35vh"}}/>
                </Zoom>

                <h2>THE CODE</h2>
                <CodeBlock text={A3code} language="arduino" theme={obsidian}/>
              </div>
            }

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 4: !visible[4]}))}}>
              <h3 className="menu-text">Assignment 4: Libraries!</h3>
            </div>
            {visible && visible[4] && 
              <div style={{textAlign: "left"}}>
                <h2>A4: A QUARTER OF MY CHESSBOARD PROJECT (why'd I chose something so hard)</h2>
                <h3>
                  So this project was a partial tech demo for my eventual final. It had two CD74HC166E shift registers
                  to expand the amount of inputs I could connect to my arduino, 16 A3144 hall effect sensors that can
                  detect my magnetic chess pieces, an lcd screen to display which sensors are active (or so I hoped),
                  and a potentiometer to control the backlight/contrast of the LCD. <br/><br/>

                  The only resistors used in this assignment were 16 10k ohm resistors, one for each of the hall sensors.
                  10k seems to be the most popular resistance for most sensors and inputs, serving the purpose of reducing the
                  amount of power wasted and protecting from onboard noise. <br/><br/>

                  Got the lcd screen kind of working! Hard getting it in focus so one of the videos only shows the lcd by itself.
                  Takes a couple seconds for the lcd to update to what the serial monitor prints.
                </h3>

                <h2>SCHEMATIC</h2>
                <Zoom>
                  <img src="a4.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>SENSORS AND BLURRY LCD</h2>
                <Zoom>
                  <ReactPlayer url='a4.mp4' playing='true' loop='true'/>
                </Zoom>
                <Zoom>
                  <ReactPlayer url='a4_lcd.mp4' playing='true' loop='true'/>
                </Zoom>
                <Zoom>
                  <img src="hw4_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw4_2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw4_3.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw4_4.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="hw4_5.jpg" style={{height: "35vh"}}/>
                </Zoom>

                <h2>THE CODE</h2>
                <CodeBlock text={A4code} language="arduino" theme={obsidian}/>
              </div>}

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 5: !visible[5]}))}}>
              <h3 className="menu-text">Assignment 5: High(er) Voltage and transistors!</h3>
            </div>
            {visible && visible[5] && 
            <div style={{textAlign: "left"}}>
                <h2>A5: I spent all my time working on chessboard so I made a simple circuit where a remote 
                  makes the LED strip blink at various speeds
                </h2>
                <h3>
                  The entire spool of LEDs was being used. 100 strips with 3 LEDs each, each strip draws 8.75 mA of current (according
                  to slide 5 in our lecture 8 notes), which means the LEDs in this circuit are drawing a total of 875 mA, or 0.875 A.
                  According to slide 10 of those same lecture notes, the MOSFET can handle 32 A of current, easily enough to handle 
                  the spool of LEDs. <br/><br/>

                  If take a gander over at the happy little schematic down below; the LED strip is connected to the drain pin on the
                  MOSFET. The LED is sending a full 12 volts (at least it should in theory, be receiving 12V. Depends on how much 
                  you trust power supplies) to the drain, and from there the mosfet does some magic to reduce the voltage to a nice 
                  5 V before sending it through the source pin and to the Arduino data pin. Without the mosfet connecting the 12V 
                  LEDs to the Arduino would likely result in the tragic death of both the lights and the arduino.
                </h3>

                <h2>SCHEMATIC</h2>
                <Zoom>
                  <img src="A5.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>LED Strip</h2>
                <Zoom>
                  <ReactPlayer url='a5.mp4' playing='true' loop='true'/>
                </Zoom>
                <Zoom>
                  <img src="a5_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a5_2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a5_3.jpg" style={{height: "35vh"}}/>
                </Zoom>
                
                <h2>THE CODE</h2>
                <CodeBlock text={A5code} language="arduino" theme={obsidian}/>
              </div>}

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 6: !visible[6]}))}}>
              <h3 className="menu-text">Assignment 6: Talking to the Web!</h3>
              
            </div> 

            {visible && visible[6] && <div style={{textAlign: "left"}}>
              <h2>A6: I was too busy taking care of my kitty so here's a very rushed assignment
                </h2>
                <h3>
                  Analog stick moves circle, once it gets to a certain spot the javascript sends serial back to arduino to turn on blue light.
      
                </h3>

                <h2>SCHEMATIC</h2>
                <Zoom>
                  <img src="a6.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>Circle</h2>
                <Zoom>
                  <ReactPlayer url='a6.mp4' playing='true' loop='true'/>
                </Zoom>
                <h3>It's a little unreliable, but it does turn on eventually if the circle
                  is left on the left side long enough.
                </h3>
                
                <h2>THE CODE</h2>
                <h3>Arduino</h3>
                <CodeBlock text={A6acode} language="arduino" theme={obsidian}/>
                <h3>Javascript</h3>
                <CodeBlock text={A6jcode} language="javascript" theme={obsidian}/>
                <h3>html</h3>
                <CodeBlock text={A6hcode} language="html" theme={obsidian}/>
              
              </div>}
        
              <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 7: !visible[7]}))}}>
              <h3 className="menu-text">Chessboard! (Work in progress)</h3>
              
            </div> 

            {visible && visible[7] && <div style={{textAlign: "left"}}>
              <h2>Chessboard: At one point part of it worked!
                </h2>
                <h3>
                  At the moment I'm still focusing on just the hardware and haven't progressed to any actual chess.
                  Right now each hall sensor has a LED, so if the sensor detects a magnet it will turn on it's respective
                  LED. <br></br><br></br>

                  Once all the sensors and LEDs are wired up, I'll write code that converts the sensor data 
                  into chess coordinates, which will then be sent through USB serial to a raspberry pi that will 
                  process the data and send back new coordinates. The arduino will then light up the LED and whatever
                  coordinates the PI sent back to indicate to the player where the board wants to move.
                </h3>

                <h2>SCHEMATIC</h2>
                <Zoom>
                  <img src="Chessboard.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>Images and Videos</h2>
                <Zoom>
                  <ReactPlayer url='v1.mp4' playing='true' loop='true' volume="0"/>
                </Zoom>
                <Zoom>
                  <ReactPlayer url='v2.mp4' playing='true' loop='true' volume="0"/>
                </Zoom>
                <Zoom>
                  <img src="model.png" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c3.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c4.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c5.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c6.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c7.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c8.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c9.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c10.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="c11.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="fire.jpg" style={{height: "35vh"}}/>
                </Zoom>
                
                <h2>THE CODE</h2>
                <h3>Arduino</h3>
                <CodeBlock text={ChessCode} language="arduino" theme={obsidian}/>
              </div>}
          </div>
        </div>

    </div>
  );
}

export default App;
